import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import { post, tokenpost } from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue';
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '我的',
			changestatus: 0,
			login_status: false,
			wxShowNum:0,
			ava_url: '',
			dataList: {
				loginname: '',
				realname: '',
				telephone: '',
				integral: 0,
				companyname: '',
				expiredate: '',
				daycount: 0,
				ordercount: '0',
				ismain: 0
			}
		};
	},
	onLoad() {
		uni.$on('changecompany', this.loadInit);
		this.getwxInfo();
	},
	onShow() {
		this.loadInit();
		const isLogin =uni.getStorageSync('islogin');
		this.login_status = isLogin=='1'?true:false;
		console.log(this.login_status);
	},
	onUnload() {
		uni.$off('changecompany');
	},
	methods: {
		loadInit() {
			this.tokenRefresh();
			this.loadData();
		},
		getwxInfo() {
			var that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							that.ava_url = infoRes.userInfo.avatarUrl;
						}
					});
				}
			});
		},
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handlePassword() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/login/editPassword'
			});
		},
		handlePrint() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/print/print'
			});
		},
		handleCall(){
			uni.makePhoneCall({
			    phoneNumber: '4001565661' //仅为示例
			});
		},
		handleBankSet() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/account/accountlist'
			});
		},
		handleAbout() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/about/about'
			});
		},
		handleRecharge() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/recharge/recharge'
			});
		},
		handleUserManage() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/user/user'
			});
		},
		handleTime() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/givetime'
			});
		},
		handleIntegral() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/integral/integral'
			});
		},
		handleShare() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/share/share'
			});
		},
		handlewx() {
			uni.navigateTo({
				url: '/pages/my/wxSet/wxSet'
			});
		},
		//退出登录
		handleLogout() {
			uni.showModal({
				title: '确定要退出登录么？',
				success: e => {
					if (e.confirm) {
						try {
							uni.removeStorage({
								key: 'userInfo',
								success: function(res) {
									uni.removeStorageSync('islogin');
									uni.removeStorageSync('currentUnitList');
									uni.removeStorageSync('productList');
									uni.removeStorageSync('productCategory');
									setTimeout(() => {
										const userInfo = uni.getStorageSync('userInfo');
										if (!userInfo) {
											uni.reLaunch({
												url: '/pages/my/my'
											});
										}
									}, 200);
								}
							});
						} catch (e) {}
					}
				}
			});
		},
		//设置
		handleSet() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '../my/set'
			});
		},
		tokenRefresh() {
			const userInfo = uni.getStorageSync('userInfo');
			tokenpost(api.tokenRefresh)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						let refresh_userInfo = {
							token: res.data.data.token,
							exp: res.data.data.exp,
							userId: res.data.data.userId
						};
						uni.setStorage({
							key: 'userInfo',
							data: refresh_userInfo,
							success: function() {}
						});
						uni.setStorageSync('islogin', '1');
					} else {
						uni.setStorageSync('islogin', '0');
					}
				})
				.catch(error => {
					//this.$api.msg('token刷新失败');
				});
		},
		loadData() {
			this.$refs.loading.open();
			tokenpost(api.GetUserInfo)
				.then(res => {
					if (res.status == 200) {
						this.$refs.loading.close();
						if (res.data.returnCode == '0000') {
							this.dataList = res.data.data;
							this.login_status = true;
							if (this.dataList.companyname == '') {
								uni.navigateTo({
									url: '../my/set'
								});
							}
							if((this.dataList.wechatopenid == null || this.dataList.wechatopenid == '' ) && (this.wxShowNum==0)){
								this.wxShowNum = 1;
								uni.navigateTo({
									url: '../my/wxSet/wxSet'
								});
							}
						} else {
							//this.$api.msg(res.data.returnMessage);
							this.dataList = { loginname: '', realname: '', telephone: '', companyname: '', expiredate: '', daycount: 0, ordercount: '0' };
							this.login_status = false;
						}
					} else {
						
						this.$api.msg(res.data.returnMessage);
					}
				})
				.catch(error => {
					this.login_status=false
					this.$refs.loading.close();
					this.$api.msg('请求失败fail');
				});
		}
	}
};
